import axios, {AxiosRequestConfig} from 'axios'

const ENDPOINT_API: string = "https://ukttorn0nl.execute-api.ap-northeast-2.amazonaws.com/gatsby-test"


// export function axiosAsyncConfig(resource: string, config: Params) : [string, any]{
export function axiosAsyncConfig(resource: string, config: Params) : [string, AxiosRequestConfig]{
    return [ENDPOINT_API + resource, {
        data: config,
        headers: { "Content-Type": "application/json" },
    }]
}

export interface Params {
    name: string,
    mobile: string,
    email: string
}

