export const paramGetFormContentsTest: Params = ({
    name: 'lee chanhee1111',
    mobile: '111-1111-1111',
    email: 'hello1111@gmail.com',

  });
export function dataGetFormContents(userName: string, userMobile: string, userEmail: string) : Params
{
    return ({
        name: userName,
        mobile: userMobile,
        email: userEmail,
    })
};
